<template>
        <!--  -->
        <div style="width:500px;height:100px">
            <video-player  class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions"
            ></video-player>

            <video-player  class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions2"
            ></video-player>
        </div>
</template>
<script>
import "vue-video-player/node_modules/video.js/dist/video-js.css"

require('vue-video-player/src/custom-theme.css');
//import 'videojs-flash'
import poster from "../assets/img/i-xrays.png";
import src from "../assets/music.mp4";
import src2 from "../assets/music2.mp4";
export default {
  name: 'sin',
  data () {
    return {
        playerOptions : {
            playbackRates: [0.7, 1.0, 1.5, 2.0,10], //
            autoplay: false, //true,
            muted: false, // 
            loop: false, // 
            preload: 'auto', // <video>auto,
            language: 'zh-CN',
            aspectRatio: '16:9', //  - "16:9""4:3"
            fluid: true, // trueVideo.js player
            sources: [{
            //type: "rtmp/mp4",
           // src: "rtmp://live.hkstv.hk.lxdns.com/live/hks" //url
            src:src
            }],
           // techOrder: ['flash'],
            poster: poster, //
             width: document.documentElement.clientWidth,
            notSupportedMessage: '', //Video.js
            controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //
            }
        },
        playerOptions2 : {
            playbackRates: [0.7, 1.0, 1.5, 2.0,10], //
            autoplay: false, //true,
            muted: false, // 
            loop: false, // 
            preload: 'auto', // <video>auto,
            language: 'zh-CN',
            aspectRatio: '16:9', //  - "16:9""4:3"
            fluid: true, // trueVideo.js player
            sources: [{
            //type: "rtmp/mp4",
           // src: "rtmp://live.hkstv.hk.lxdns.com/live/hks" //url
            src:src2
            }],
           // techOrder: ['flash'],
            poster: poster, //
             width: document.documentElement.clientWidth,
            notSupportedMessage: '', //Video.js
            controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //
            }
        }
    }
  },
  components: {
    
  },
  mounted() {
     
  },
  methods:{
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style> 
 
 
 
 


